import { Values } from '../components/ModalForm/ModalForm';
import { todoList } from './todoList';

interface Edit{
  id: number;
  value: Values;
}

interface Actions {
  type: string;
  payload?:
    | Values
    | Edit
    | number
    | string
    | [number, number | null]
    | [string, number | null];
  edit?: Edit;
  id?: number;
}

const initState = {
  currentCategoryId: 1,
  currentTaskId: 1,
  isVisibleModal: false,
  lastCatId: 1,
  lastTaskId: 8,
  categoryes: [
    {
      id: 1,
      description: 'Category 1',
      isCompleted: false,
      createdAt: new Date().toLocaleString('en-US'),
      tasksList: todoList,
    },
  ],
};
export interface TasksList {
  id: number;
  titleDescription: string;
  description: string;
  isCompleted: boolean;
  createdAt: string;
  updatedAt: string;
  priority: string;
}

export interface Categoryes {
  id: number;
  description: string;
  createdAt: string;
  isCompleted: boolean;
  tasksList: Array<TasksList>;
}

export interface StateInit {
  currentCategoryId: number | null;
  currentTaskId: number;
  isVisibleModal: boolean;
  lastCatId: number;
  lastTaskId: number;
  categoryes: Array<Categoryes>;
}



class TaskItem {
  static _lastId: number | null = null;
  static _autoId: boolean = true;
  public id: number;
  public titleDescription: string;
  public description: string;
  public createdAt: string;
  public updatedAt: string;
  public priority: string;
  public isCompleted: boolean;
  constructor(values: Values, lastId: number = 1, isAutoID?: boolean) {
    TaskItem._autoId = (isAutoID === undefined)? true: isAutoID;
    if (TaskItem._lastId === null) TaskItem._lastId = lastId;
    this.id = (TaskItem._autoId)? this._createNewID() : lastId;
    this.titleDescription = values.inputValue;
    this.description = values.memoValue;
    this.createdAt = TaskItem._getNewDate();
    this.updatedAt = TaskItem._getNewDate();
    this.priority = this._convertToString(values.radioBox);
    this.isCompleted = false;
  }
  _createNewID(): number {
    if (TaskItem._lastId !== null) TaskItem._lastId += 1;
    else TaskItem._lastId = 1;
    return TaskItem._lastId;
  }
  _convertToString(p: Array<boolean>): string {
    const priority = ['normal', 'high', 'urgent'];
    return priority[p.findIndex((i) => i)];
  }
  static _getNewDate(){
    const date = new Date();
    const D = date.getDate().toString().padStart(2,'0');
    const M = date.getMonth().toString().padStart(2,'0');
    const Y = date.getFullYear().toString();
    const h = date.getHours().toString().padStart(2,'0');
    const m = date.getMinutes().toString().padStart(2,'0');
    const s = date.getSeconds().toString().padStart(2,'0');
    const dateString = `${D}.${M}.${Y} - ${h}:${m}:${s}`;
    return dateString;
  }
}

class EditableTaskItem extends TaskItem{
  constructor(values: Values, taskItem: TasksList){
    super(values, taskItem.id , false);
    this.createdAt = (taskItem.createdAt)
      ? taskItem.createdAt
      : TaskItem._getNewDate();
    this.updatedAt = TaskItem._getNewDate();
  }
}

class CategoryItem {
  static _lastId: number | null = null;

  public id: number;
  public description: string;
  public isCompleted: boolean;
  public createdAt: string;
  public tasksList: [];
  constructor(title: string, lastId: number = 1) {
    if (CategoryItem._lastId === null) {
      CategoryItem._lastId = lastId;
    }
    this.id = this.createNewID();
    this.description = title;
    this.isCompleted = false;
    this.createdAt = this.getDate();
    this.tasksList = [];
  }
  getDate() {
    return new Date().toLocaleString('en-US');
  }
  createNewID() {
    if (CategoryItem._lastId !== null) CategoryItem._lastId += 1;
    else CategoryItem._lastId = 1;
    return CategoryItem._lastId;
  }
}

function numberFrom(str: string) {
  const pr = ['normal', 'high', 'urgent'];
  const idx = pr.indexOf(str);
  return idx !== -1 ? idx : 0;
}

function sortTasksByPriority(
  tasksList: TasksList[],
  sign: boolean = false
): true {
  if (tasksList.length > 0) {
    tasksList.sort((a: TasksList, b: TasksList) => {
      if (sign) return numberFrom(a.priority) - numberFrom(b.priority);
      return numberFrom(b.priority) - numberFrom(a.priority);
    });
  }
  return true;
}
function sortTasksByIdOfPriority(
  tasksList: TasksList[],
  sign: boolean = false
): true {
  if (tasksList.length > 0) {
    tasksList.sort((a: TasksList, b: TasksList) => {
      if (a.priority === b.priority) {
        if (sign) return numberFrom(a.priority) - numberFrom(b.priority);
        return numberFrom(b.priority) - numberFrom(a.priority);
      }
      return 0;
    });
  }
  return true;
}

function getCategoryIndex(catList: Categoryes[], id: number): number | null {
  const idx = catList.findIndex((cat) => cat.id === id);
  return idx !== -1 ? idx : null;
}

function getCurrentCatItem(catList: Categoryes[], id: number | null){
  if(id !== null){
    const curItem = catList.find(c => c.id === id)
    return curItem;
  }
}
function getCurrentTaskItem(tasksList: TasksList[], id: number | null){
  if(id !== null){
    const curItem = tasksList.find(t => t.id === id)
    return curItem;
  }
}

function getPriority(pr: boolean[]){
  const status = ['normal','high','urgent'];
  return status[pr.indexOf(true)];
}

const reducer = function (state: StateInit = initState, action: Actions) {
  const newState = { ...state };
  switch (action.type) {
    case 'ADD_CATEGORY':
      {
        const valuesArr = action.payload as Values;
        const title: string = valuesArr.inputValue;
        const newCategory = new CategoryItem(title, state.lastCatId);
        newState.categoryes.push(newCategory);
        newState.currentCategoryId = newCategory.id;
        newState.lastCatId = newCategory.id;
        newState.isVisibleModal = true;
      }
      break;
    case 'SELECT_CATEGORY':
      {
        if (typeof action.payload === 'number')
          newState.currentCategoryId = action.payload;
      }
      break;
    case 'DELETE_CATEGORY_ITEM':
      {
        const idx = newState.categoryes.findIndex(
          (i) => i.id === action.payload
        );
        newState.categoryes.splice(idx, 1);
        newState.currentCategoryId =
          newState.categoryes.length > 0 ? newState.categoryes[0].id : null;
      }
      break;
    case 'EDIT_CATEGORY_TITLE':
      {
        let [title, id] = action.payload as [string, number | null];
        const editIndex = newState.categoryes.findIndex((i) => i.id === id);
        if (editIndex !== -1) {
          newState.categoryes[editIndex].description = title;
        }
      }
      break;
    case 'REODER_CATEGORY':
      {
        const [source, dest] = action.payload as [number, number | null];
        if (dest !== null) {
          const item = newState.categoryes.splice(source, 1)[0];
          newState.categoryes.splice(dest, 0, item);
        }
      }
      break;
    case 'ADD_TASK_ITEM':
      {
        if (action.payload) {
          const values = action.payload as Values;
          const newTaskItem = new TaskItem(values, state.lastTaskId);
          const catID = newState.currentCategoryId;
          console.log(newTaskItem);
          const currentCategory = newState.categoryes.find(
            (c) => c.id === catID
          );
          if (currentCategory) {
            currentCategory.tasksList.push(newTaskItem);
            sortTasksByPriority(currentCategory.tasksList);
            sortTasksByIdOfPriority(currentCategory.tasksList);
          }
        }
      }
      break;
    case 'TASK_DONE_TOGGLE':
      {
        const taskId = action.payload as number;
        const catID = newState.currentCategoryId;
        if (catID !== null) {
          const curerentCategory = newState.categoryes.find(
            (c) => c.id === catID
          );
          if (curerentCategory) {
            let currentTask = curerentCategory.tasksList.find(
              (t) => t.id === taskId
            );
            if (currentTask) {
              console.log('toggle task:---', currentTask);
              const newTask = {
                ...currentTask,
                isCompleted: !currentTask.isCompleted,
              };
              const newList = [...curerentCategory.tasksList];
              const idx = newList.findIndex((t) => t.id === taskId);
              newList.splice(idx, 1, newTask);
              curerentCategory.tasksList = newList;
            }
          }
        }
      }
      break;
    case 'EDIT_TASK':
      {
        if(action.edit){
          const values = action.edit.value;
          const tId = action.edit.id;
          // const newTaskItem = new EditableTaskItem(values, tId);
          const catID = newState.currentCategoryId;
          const currentCategory = newState.categoryes.find(
            (c) => c.id === catID
          );
          if (currentCategory) {
            const newTasksList = [...currentCategory.tasksList];
            const taskItem = newTasksList.find(t => t.id === tId);
            if(taskItem){
              const newTaskItem = new EditableTaskItem(values, taskItem);
              // const taskItem = newTasksList.find(t => t.id === tId);
              const tskIDX = newTasksList.findIndex(t => t.id === tId);
              // const editedTaskItem = {...taskItem} as TasksList;
              // editedTaskItem.titleDescription = action.edit.value.inputValue;
              // editedTaskItem.description = action.edit.value.memoValue;
              // editedTaskItem.priority = getPriority(action.edit.value.radioBox);

              newTasksList.splice(tskIDX,1,newTaskItem);
              currentCategory.tasksList = newTasksList;

              sortTasksByPriority(currentCategory.tasksList);
              sortTasksByIdOfPriority(currentCategory.tasksList);
            }
          }
        }
      }
      break;
    case 'DELETE_TASK_ITEM': {
      const catID: number | null = newState.currentCategoryId;
      const curCatItem: Categoryes | undefined = newState.categoryes.find(c => c.id === catID);
      if(curCatItem){
        const taskIDX = curCatItem.tasksList.findIndex(t => t.id === action.id);
        const newTaskList = [...curCatItem.tasksList];
        newTaskList.splice(taskIDX,1);
        curCatItem.tasksList = newTaskList;
        sortTasksByPriority(curCatItem.tasksList);
        sortTasksByIdOfPriority(curCatItem.tasksList);
      }
    }
  }
  console.log('cat id:', newState);
  return newState;
};

export { reducer };
